/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  background-color: #333333;
  padding: 0rem 0 2rem !important;
}

img {
  width: 7rem;
}

.footer {
  background-color: #333333;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.footer .footerContenedor {
  padding-top: 2rem;
}
.footer .footerContenedor .nav {
  justify-content: space-evenly;
}

.barraNegra {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0;
  background-color: #000000;
  justify-items: center;
}
@media (min-width: 768px) {
  .barraNegra {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1024px) {
  .barraNegra {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.barraNegra .socialMedia {
  display: none;
}
@media (min-width: 1024px) {
  .barraNegra .socialMedia {
    display: block;
  }
}
.barraNegra .menu-top {
  display: flex;
  margin: 0rem;
  list-style-type: none;
  gap: 1rem;
}
.barraNegra .menu-top a {
  color: #FFFFFF;
}

.barra {
  display: grid;
  justify-items: center;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    grid-template-columns: 1fr 3fr;
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}
.nav a {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .nav a {
    font-size: 2rem;
  }
}
.nav p {
  display: none;
}
@media (min-width: 768px) {
  .nav p {
    display: block;
  }
}

.contenido {
  display: grid;
  grid-template-columns: 1fr 3fr;
  margin: 2rem;
  justify-items: center;
  gap: 3rem;
}

.menu {
  width: 100%;
  height: auto;
  background-color: #333333;
}
.menu ul {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  margin: 0rem;
  list-style-type: none;
  padding: 0;
  position: relative;
}
.menu ul a {
  background: #888787;
  margin: 0.5rem;
  color: #000000;
  text-align: center;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu ul a:first-of-type {
  background: #000000;
  color: #FFFFFF;
}
.menu ul li {
  padding: 0.5rem;
  font-weight: 9000;
  font-size: 2.2rem;
}

.options {
  width: 100%;
  height: auto;
  background-color: #333333;
  gap: 1rem;
}
.options ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  margin: 3rem;
  list-style-type: none;
  padding: 0;
  gap: 4rem;
}
.options li {
  background-color: #888787;
}
.options li a {
  width: auto;
  color: #000000;
}
.options li a img {
  width: 100%;
}
.options li h3 {
  color: #000000;
  margin: 0;
}
.options li p {
  margin: 0.5rem 1rem;
  color: #000000;
}
.options li .existencia {
  background-color: #4460bd;
  width: 50%;
  font-size: 15px;
  color: #FFFFFF;
  font-weight: 900;
  text-align: center;
  margin: 0.5rem 1rem;
  padding: 0 0.5rem;
  border-radius: 0.5rem;
}
.options li .opciones {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
}
.options li .opciones a {
  border: 1px solid #000;
  padding: 0 0.5rem;
}
.options li .opciones button {
  border: 1px solid #000;
}